body {
  margin: 0;
  padding: 0;
  font-family: 'Baloo Bhai', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

a {
  text-decoration: none;
  color: inherit; }

.main {
  background-color: #323232;
  width: 100%;
  height: 100vh;
  display: flex; }

.create-poll {
  margin: auto;
  width: 650px;
  display: flex;
  flex-direction: column;
  background-color: #ffd756;
  padding: 10px 10px; }
  .create-poll .error {
    text-align: center;
    font-size: 24px;
    margin: 0; }
  .create-poll .off-button {
    opacity: .5; }
  .create-poll .poll-content {
    display: flex;
    height: 100%;
    flex-direction: column;
    margin: 10px; }
    .create-poll .poll-content .master-div {
      width: 100%;
      display: flex;
      flex-direction: column; }
      .create-poll .poll-content .master-div .subdiv {
        display: flex;
        width: 100% !important; }
        .create-poll .poll-content .master-div .subdiv .circle {
          margin: auto 10px auto auto;
          height: 8px;
          width: 8px !important;
          border-radius: 50%;
          border: solid 1px rgba(0, 0, 0, 0.55);
          background-color: rgba(0, 0, 0, 0.55); }
        .create-poll .poll-content .master-div .subdiv input {
          width: 100%; }
      .create-poll .poll-content .master-div .custom-error {
        width: 100% !important; }

.submit-button {
  background-color: #4CAF50;
  border: none;
  color: white;
  padding: 10px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px; }

.input-design {
  background-color: transparent;
  padding: 5px 5px;
  margin-top: 15px;
  margin-bottom: 15px;
  border: none;
  border-bottom: 1px solid green;
  outline: none; }

.question {
  height: 35px;
  font-size: 26px; }

.results {
  width: 75%;
  display: flex;
  min-height: 575px; }

.view-error {
  width: 35%; }

.vote-poll {
  display: flex;
  min-height: 200px; }
  .vote-poll .poll-questions {
    display: flex;
    flex-direction: column; }

.vote-success {
  display: flex;
  flex-direction: column;
  padding: 15px 15px; }
  .vote-success .poll-question {
    text-transform: capitalize;
    margin-left: auto;
    margin-right: auto; }
  .vote-success .poll-questions .quest {
    margin: 5px 10px 5px 10px; }
  .vote-success .poll-questions label {
    margin-left: 5px; }
  .vote-success button {
    margin-top: 15px;
    width: 115px; }

.vote-error {
  display: flex;
  flex-direction: column;
  height: 350px;
  position: relative; }
  .vote-error span {
    margin: 10px auto 10px auto;
    font-size: 24px; }
  .vote-error .error-button {
    position: absolute;
    bottom: 10px;
    left: 10px;
    border: solid 1px black;
    padding: 10px 10px; }

nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 55px;
  background-color: #ffd756;
  display: flex; }
  nav .brand-logo {
    margin: auto auto auto 25px; }
    nav .brand-logo h4 {
      font-size: 36px;
      margin: 0; }

.master-share {
  background-color: #ffd756;
  position: fixed;
  right: 0;
  bottom: 0;
  border-top-left-radius: 10px;
  -webkit-transition: 0.5s ease;
  transition: 0.5s ease; }
  .master-share .inner-share {
    padding: 10px 10px;
    display: flex;
    flex-direction: column;
    width: 375px; }

.hide {
  right: -500px; }

.err div {
  height: 100%;
  display: flex; }
  .err div span {
    margin: auto; }

